import { Injectable, Type } from '@angular/core';
import { IDisposable, IEmitable } from './types';
import { EventBusPipe } from './frm-eventpipe';
import { Subject } from 'rxjs';

@Injectable()
export class FrmEventBus implements IEmitable {

    public eventMap: Map<string, Array<EventBusPipe>>;

    constructor() {
        this.eventMap = new Map<string, Array<EventBusPipe>>();
    }


    // åéäºä»¶ï¼éç¥è®¢éèæ¥æ¶æ¶æ¯ã
    post(tokenValue: string, eventArgs: any): void {
        let eventPipeList = this.eventMap.get(tokenValue);
        if (!eventPipeList) {
            eventPipeList = new Array<EventBusPipe>();
            this.eventMap.set(tokenValue, eventPipeList);
        }
        let eventPipe = eventPipeList.find(item => item.examByTargetToken(tokenValue));
        if (!eventPipe) {
            eventPipe = new EventBusPipe(tokenValue, eventPipeList, null);
        }
        for (const eventPipeDetail of eventPipeList) {
            if (eventPipeDetail.matchEmitterToken(tokenValue)) {
                eventPipeDetail.post(eventArgs);
            }
        }
    }

    initialPipe(token: string, sub: Subject<any>) {
        let pipes = this.eventMap.get(token);
        if (!pipes) {
            pipes = new Array<EventBusPipe>();
            this.eventMap.set(token, pipes);
        }
        let eventPipe = pipes.find(item => item.examByTargetToken(token));
        if (!eventPipe) {
            eventPipe = new EventBusPipe(token, pipes, sub);
        }
    }

    // è®¢éäºä»¶
    on(tokenValue: string, handler: (value: any) => void): IDisposable {
        const pipe = this.getEventPipe(tokenValue);
        if (pipe) {
            return pipe.subscribe(handler);
        }
    }

    /** è®¢éäºä»¶ï¼å¹¶è®°å½è®¢éèå¯ä¸æ è¯ */
    onWithObkey(tokenValue: string, observerKey: string, handler: (value: any) => void): IDisposable {
        const pipe = this.getEventPipe(tokenValue);
        if (pipe) {
            return pipe.subscribeWithObkey(observerKey, handler);
        }
    }

    // è®¢éä¸æ¬¡ãæ¥æ¶å°ä¸æ¬¡æ¶æ¯ä¹åèªå¨åæ¶è®¢é
    once(tokenValue: string, handler: (value: any) => void): IDisposable {
        const pipe = this.getEventPipe(tokenValue);
        if (pipe) {
            const res = pipe.subscribeOnce(handler);
            this.eventMap.delete(tokenValue);
            return res;
        }
    }

    unSubscribe(tokenValue: string) {
        const pipe = this.getEventPipe(tokenValue);
        if (pipe) {
            pipe.unSubscribe();
        }
    }

    private getEventPipe(tokenValue: string) {
        const eventPipeList = this.eventMap.get(tokenValue);
        if (!eventPipeList) {
            return null;
        }
        const eventPipe = eventPipeList.find(item => item.examByTargetToken(tokenValue));
        return eventPipe;
    }
}
