@Injectable()
export class FrameworkService {

    private FUNCSTATUS_PATH = '/api/runtime/sys/v1.0/function-states';
    public BeforeFuncClose = EventUtil.BEFORE_FUNC_CLOSE;
    public FuncClosed = EventUtil.FUNC_CLOSED;
    public FuncSwitch = EventUtil.FUNC_SWITCH;

    constructor(
        private http: HttpService,
        private router: Router,
        private cache: CacheService,
        private sessionSvc: SessionService,
        private feb: FrmEventBus,
        private funcService: FuncsService,
        private loadService: LoadingService,
        @Optional() private appService: AppService,
        @Optional() private i18nSetting: FrmI18nSettingService
    ) {
        if (!this.i18nSetting) {
            const variables = new FrameworkVariableService();
            const language = new LanguageService(null);
            this.i18nSetting = new FrmI18nSettingService(variables, language, http);
        }
        if (!this.appService) {
            this.appService = new AppService(this.http, this.sessionSvc, this.i18nSetting);
        }
    }

    /**
     * æ ¹æ®funcidæå¼åè½èå
     * (éç¨äºåé¡µç­¾è·¯ç±ï¼ä¸æ¯æNG7åçº§åèåä¹é´è·³è½¬)
     */
    openFunc(funcid: string, routepath: string, parentsessionid: string, newsession: boolean = true, simpleparam: string = ''): any {

        const appOps: AppOptions = {
            appType: AppType.Menu,
            funcId: funcid,
            appId: '',
            appEntrance: ''
        };

        this.internalOpen(appOps);
        return of(true);
        // return this.internalOpen$(appOps);
    }

    /**
     * ç´æ¥æå¼åè½
     */
    public openMenu(options: AppOptions) {
        switch (options.appType && options.appType.toLowerCase()) {
            case AppType.App:
                if (!options.appId) {
                    window.alert('åæ°appIdä¸è½ä¸ºç©ºï¼');
                    return;
                }
                this.appService.openAppByOptions(options);
                break;
            case AppType.Menu:
                this.internalOpen(options);
                break;
            case AppType.Other: break;
        }
    }

    /**
     * éè¿æµæ¹å¼æå¼åè½
     */
    public openMenu$(options: AppOptions) {
        switch (options.appType && options.appType.toLowerCase()) {
            case AppType.App:
                if (!options.appId) {
                    window.alert('åæ°appIdä¸è½ä¸ºç©ºï¼');
                    return;
                }
                return this.appService.openAppByOptions$(options);
            case AppType.Menu:
                if (options.onlyGetIframeLink === true) {
                    return this.internalOpenByIframeLink$(options);
                }
                return this.internalOpen$(options);
            case AppType.Other: break;
        }
    }

    /**
     * æå¼èæ¥èå
     */
    openConnectedFunc(funcId: string, options: OpenfuncOptions) {
        const appOps: AppOptions = {
            appType: AppType.Menu,
            funcId,
            appId: '',
            appEntrance: '',
            entityParams: options.EntityParam,
        };

        this.internalOpen(appOps);
    }

    /**
     * æå¼èæ¥èåï¼å¹¶ä¼ å
     */
    public openFuncWithParam(funcId: string, params: Map<string, string>, reload?: boolean) {
        const options: AppOptions = {
            appType: AppType.Menu,
            funcId,
            appId: '',
            appEntrance: '',
            entityParams: params,
            queryStringParams: params,
            isReload: reload
        };
        this.internalOpen(options);
    }

    /**
     * æå¼åè½ï¼ä¸è¿åæµ
     */
    private internalOpen(options: AppOptions) {
        this.checkingBeforeOpen$(options)
            .subscribe(
                res => {
                    if (!res || !res.func || res.linkobj) {
                        return;
                    }
                    const func = res.func;
                    this.getInvokeAndEnterFunc$(options, func).subscribe(
                        (lobj: IframeLink) => this.getFeb().post(EventUtil.FARRIS_APP_CLICK, lobj),
                        err => {
                            const info = {
                                isErr: true,
                                errInfo: err
                            };
                            this.getFeb().post(EventUtil.FARRIS_APP_CLICK, info);
                        }
                    );
                }
            );
    }

    /**
     * éè¿ä¸²æµæ§å¶ï¼æå¼èå
     */
    private internalOpen$(options: AppOptions): Observable<any> {
        return this.checkingBeforeOpen$(options)
            .pipe(
                switchMap(
                    res => {
                        if (!res || !res.func || res.linkobj) {
                            return of(null);
                        }
                        const func = res.func;
                        return this.getInvokeAndEnterFunc$(options, func).pipe(
                            map(
                                (lobj: IframeLink) => {
                                    this.getFeb().post(EventUtil.FARRIS_APP_CLICK, lobj);
                                    return true;
                                }
                            )
                        );
                    }
                )
            );
    }

    /**
     * éè¿ä¸²æµè·åè¢«æå¼èåçiframeLinkå¯¹è±¡ä¿¡æ¯
     */
    private internalOpenByIframeLink$(options: AppOptions): Observable<IframeLink> {

        if (!options.funcId) {
            window.alert('åæ°funcIdä¸è½ä¸ºç©ºï¼');
            return of(null);
        }

        return this.getFuncEntity(options.funcId).pipe(
            switchMap(
                func => {
                    if (!func) {
                        window.alert(`è¯·ç¡®è®¤æ¨æ¯å¦å·å¤æå¼èå(ID:${options.funcId})çæé`);
                        return null;
                    }
                    return this.getInvokeAndEnterFunc$(options, func);
                }
            ));
    }

    private getInvokeAndEnterFunc$(options: AppOptions, func: any): Observable<IframeLink> {
        const newPageId = this.newPageId(options.funcId, options.tabId);
        options.tabId = newPageId;
        let linkobj: IframeLink;
        return this.getFuncInvok(options.funcId).pipe(
            switchMap(
                funcInvok => {

                    const routurl = funcInvok.invokingConfig.url;
                    const paramInfo = funcInvok.invokingConfig.staticParams;
                    const entityParamInfo = funcInvok.invokingConfig.entityParams;
                    const staticParam = paramInfo ? JSON.parse(paramInfo) : '';
                    const entityParam = entityParamInfo ? JSON.parse(entityParamInfo) : '';

                    const su = this.getSu(routurl);
                    return this.enterFunc({
                        tabId: newPageId,
                        funcId: options.funcId,
                        menuName: options.tabName || func.name,
                        action: 'enter'
                    }, su).pipe(
                        map(
                            formToken => {
                                linkobj = new IframeLink(
                                    func.code,
                                    null,
                                    this.buildQueryStringByMap(routurl, options.funcId, options.tabId,
                                        formToken, options.queryStringParams),
                                );
                                linkobj.id = newPageId; // é¡µé¢å¯ä¸æ è¯ åè®¸é¡µé¢éå¤æå¼
                                linkobj.appType = AppType.Menu;
                                linkobj.funcId = options.funcId;
                                linkobj.FuncName = options.tabName || func.name;
                                linkobj.url = funcInvok.invokingConfig.url;
                                linkobj.reload = options.isReload;
                                linkobj.su = su;
                                linkobj.isNewTab = options.isNewTab;
                                linkobj.formToken = formToken;

                                if (staticParam) {
                                    // dkyç¹æ®åºæ¯ï¼æå¼jqueryåçåè½
                                    staticParam.forEach(
                                        item => {
                                            if (item.name === 'jquery') {
                                                linkobj.isjquery = true;
                                            }
                                            if (item.name === 'mode') {
                                                linkobj.mode = item.value;
                                                linkobj.src += (item.value ? `&appMode=${item.value}&` : '');
                                            }
                                        }
                                    );
                                }
                                // å¤§å¯¹è±¡å®ä½ä¼ åï¼éå¯¹è¢«æå¼é¡µé¢å»ºç«å¯ä¸ç®¡é
                                this.getFeb().post(newPageId, options.entityParams);
                                // this.getFeb().post(EventUtil.FARRIS_APP_CLICK, linkobj);
                                return linkobj;
                            }
                        ),
                        switchMap(
                            formLink => {
                                return this.i18nSetting.getSetting$().pipe(
                                    map(
                                        () => formLink
                                    )
                                )
                            }
                        )
                    );
                }
            )
        );
    }


    /**
     * æµæ¹å¼æ£æ¥èåæå¼ç¶æ
     */
    private checkingBeforeOpen$(options: AppOptions): Observable<any> {
        if (!options.funcId) {
            window.alert('åæ°funcIdä¸è½ä¸ºç©ºï¼');
            return of(null);
        }

        return this.getFuncEntity(options.funcId).pipe(map(
            func => {
                if (!func) {
                    window.alert(`è¯·ç¡®è®¤æ¨æ¯å¦å·å¤æå¼èå(ID:${options.funcId})çæé`);
                    return null;
                }

                const funcId = options.funcId;
                const tabId = options.tabId;
                const pageId = this.getPageId(funcId, tabId);
                const linkobj = FrmUtil.getTabArray().find(i => i.id === pageId && i.appType === AppType.Menu);

                if (linkobj) {
                    linkobj.FuncName = options.tabName || linkobj.FuncName;
                    linkobj.src = this.buildQueryStringByMap(linkobj.url, funcId, options.tabId, '', options.queryStringParams);
                    linkobj.reload = options.isReload; // çé¢å·æ°ä¸æ¹session
                    this.getFeb().post(pageId, options.entityParams);
                    this.getFeb().post(EventUtil.FARRIS_APP_CLICK, linkobj);
                }
                return { func, linkobj };
            }
        ));
    }

    /**
     * å³é­å
     */
    beforeCloseMenu(options: AppOptions) {
        switch (options.appType && options.appType.toLowerCase()) {
            case AppType.App:
                this.appService.beforeCloseAppByOptions(options);
                break;
            case AppType.Menu:
                this.beforeCloseFuncByOptions(options);
                break;
            default: break;
        }
    }

    private beforeCloseFuncByOptions(options: AppOptions) {
        const func = this.funcService.getCachedFunc(options.funcId);
        const funcObkey = EventUtil.funcObsToken(options.funcId, options.tabId);
        const pageId = this.getPageId(options.funcId, options.tabId);

        const obs = EventUtil.getObserver(EventUtil.BEFORE_FUNC_CLOSE).get(funcObkey);
        if (!obs) {
            this.closeFuncByOptions(options);
            return;
        }

        const eventArgs: FuncEventArgs = {
            tabId: pageId,
            appType: AppType.Menu,
            funcId: options.funcId,
            tabName: func && func.Name,
            beforeCloseHandle: options.beforeCloseHandle
        };
        this.getFeb().post(EventUtil.BEFORE_FUNC_CLOSE, eventArgs);
    }

    /**
     * èåå³é­åäºä»¶è§¦å(æ  token)
     */
    beforeCloseFunc(funcId: string, tabId?: string) {
        const func = this.funcService.getCachedFunc(funcId);
        const funcObkey = EventUtil.funcObsToken(funcId, tabId);

        const obs = EventUtil.getObserver(EventUtil.BEFORE_FUNC_CLOSE).get(funcObkey);
        if (!obs) {
            this.closeFunc(funcId, tabId);
            return;
        }

        const eventArgs: FuncEventArgs = {
            tabId: this.getPageId(funcId, tabId),
            appType: AppType.Menu,
            funcId,
            tabName: func && func.Name
        };
        this.getFeb().post(EventUtil.BEFORE_FUNC_CLOSE, eventArgs);
    }

    /**
     * ç´æ¥å³é­
     *
     */
    closeMenu(options: AppOptions) {
        switch (options.appType && options.appType.toLowerCase()) {
            case AppType.App:
                this.appService.closeAppByOptions(options).subscribe();
                break;
            case AppType.Menu:
                this.closeFuncByOptions(options);
                break;
            default: break;
        }
    }

    private closeFuncByOptions(options: AppOptions) {
        const pageId = this.getPageId(options.funcId, options.tabId);
        EventUtil.clearObserver(EventUtil.funcObsToken(options.funcId, options.tabId));
        EventUtil.clearEventPipe(pageId); // å½åé¡µé¢å¤§å¯¹è±¡ä¼ åä¼æå¯ä¸çç®¡é
        const func = this.funcService.getCachedFunc(options.funcId);
        const objIndex = FrmUtil.getTabArray().findIndex(i => i.id === pageId && i.appType === AppType.Menu);
        const obj = FrmUtil.getTabArray()[objIndex];
        options.token = options.token || obj.formToken;
        let su = options.su;
        if (obj) {
            su = obj.su;
            FrmUtil.removeTab(objIndex);
            const eventArgs: FuncEventArgs = {
                tabId: pageId,
                appType: AppType.Menu,
                funcId: options.funcId,
                tabName: func && func.name
            };
            this.getFeb().post(EventUtil.FUNC_CLOSED, eventArgs);
        }
        this.quitFunc(options, su)
            .subscribe(() => { });
    }

    /**
     * å³é­èåtabé¡µç­¾ï¼tabIdå¯ä»¥æå®ï¼é»è®¤ä¸ºfuncId
     */
    closeFunc(funcId: string, tabId?: string): any {
        const self = this;
        const pageId = this.getPageId(funcId, tabId);
        EventUtil.clearObserver(EventUtil.funcObsToken(funcId, tabId));
        EventUtil.clearEventPipe(pageId); // å½åé¡µé¢å¤§å¯¹è±¡ä¼ åä¼æå¯ä¸çç®¡é
        const func = this.funcService.getCachedFunc(funcId);
        const objIndex = FrmUtil.getTabArray().findIndex(i => i.id === pageId && i.appType === AppType.Menu);
        const obj = FrmUtil.getTabArray()[objIndex];
        const formToken = obj.formToken;
        if (obj) {
            FrmUtil.removeTab(objIndex);
            const eventArgs: FuncEventArgs = {
                tabId: pageId,
                appType: AppType.Menu,
                funcId,
                tabName: func && func.name
            };
            self.getFeb().post(EventUtil.FUNC_CLOSED, eventArgs);
        }

        const options: AppOptions = {
            tabId,
            funcId,
            appId: '',
            appEntrance: '',
            appType: AppType.Menu,
            token: formToken,
        };
        this.quitFunc(options, obj.su)
            .subscribe(() => { }
            );
        return of(true);
    }


    /**
     * è·åèåçè°ç¨ä¿¡æ¯
     */
    getFuncInvok(funcId: string): Observable<any> {
        const invoks = this.getInvokCache();
        const funcInvok = invoks.find(f => f.id === funcId);
        if (!funcInvok) {
            return this.getInvokConfig(funcId).pipe(
                map(v => {
                    if (!v) {
                        return null;
                    }
                    invoks.push(v);
                    this.funcService.cacheAllFuncInvoks(invoks);
                    return v;
                })
            );
        }
        return of(funcInvok);
    }

    /**
     * è·åèåå®ä½
     */
    getFuncEntity(funcId: string): Observable<any> {
        const func = this.funcService.getCachedFunc(funcId);
        if (!func) {
            const url = `/api/runtime/sys/v1.0/functions/${funcId}`;
            return this.http.request('Get', url, FrmUtil.setHeader(null, {}))
                .pipe(
                    map(v => {
                        if (!v || !v[0]) {
                            return null;
                        }
                        const allFuncs = this.funcService.getAllCachedFuncs() as any[];
                        allFuncs.push(v[0]);
                        this.funcService.cacheAllFuncs(allFuncs);
                        return v[0];
                    })
                );
        }
        return of(func);
    }

    /**
     * è·åèåé¢ç½®åæ°
     */
    getPresetParams(funcId: string): Observable<any> {
        return this.getFuncInvok(funcId).pipe(
            map(funcInvok => {
                const paramInfo = funcInvok.invokingConfig.staticParams;
                const entityParamInfo = funcInvok.invokingConfig.entityParams;
                const staticParam = paramInfo ? JSON.parse(paramInfo) : [];
                const entityParam = entityParamInfo ? JSON.parse(entityParamInfo) : [];
                return this.mergeParams(staticParam, entityParam);
            })
        );
    }

    /**
     * è·åèåçéæåæ°
     */
    getStaticParam(funcId: string): Observable<any> {
        return this.getFuncInvok(funcId).pipe(
            map(v => {
                if (v.invokingConfig.staticParams) {
                    return JSON.parse(v.invokingConfig.staticParams);
                }
            })
        );
    }

    /**
     * è·åè¡¨åçå®ä½åæ°
     */
    getEntityParam(tabId: string, handler: (value: any) => void, isSubonce: boolean = true): any {
        if (isSubonce) {
            this.getFeb().once(tabId, handler);
        }
        this.getFeb().on(tabId, handler);
    }

    entityParamUnsub(funcId: string) {
        this.getFeb().unSubscribe(funcId);
    }

    /**
     * è·åæèåçå®ä½åæ°ç»æ
     */
    getEntityParamStructure(funcId: string): Observable<any> {
        return this.getFuncInvok(funcId).pipe(
            map(v => {
                if (v.invokingConfig.entityParams) {
                    return JSON.parse(v.invokingConfig.entityParams);
                }
            })
        );
    }

    private mergeParams(staticParam: any[], entityParam: any[]): Map<any, any> {
        const paraMap = new Map<any, any>();
        if (entityParam) {
            entityParam.forEach((item: any) => {
                paraMap.set(item.name, item.value);
            });
        }
        if (staticParam) {
            // funcä¸å¯¹appåæ°å¤ç¨
            staticParam.forEach((item: any) => {
                paraMap.set(item.name, item.value);
            });
        }
        return paraMap;
    }


    private getInvokConfig(funcId: string) {
        const url = `/api/runtime/sys/v1.0/functions/funcInvok/${funcId}`;
        return this.http.request('Get', url, FrmUtil.setHeader(null, {}));
    }


    private getInvokCache() {
        return this.funcService.getAllCachedFuncInvoks();
    }

    /**
     * è·åæ¡æ¶eventbus
     */
    private getFeb(): FrmEventBus {
        return EventUtil.getFrmEvent();
    }

    /**
     * æé åè½åsession
     * (1912 funcSessionç§»é¤åï¼æ¥å£ä¸åæä¾)
     */
    public buildFuncSession(funcId: string, su?: string): Observable<any> {
        const self = this;
        const userSession = this.sessionSvc.getUserSessionId();
        const body = {
            token: userSession,
            funcId,
            action: 'enter'
        };

        const bizContextUrl = this.FUNCSTATUS_PATH + (!su ? '' : `?su=${su}`);
        return this.http.request('Post', bizContextUrl, FrmUtil.setHeader(userSession, { body })).pipe(
            map((v: any) => {
                const ss: string = v.sessionId;
                if (ss !== undefined && ss !== null && ss.length > 0) {
                    self.sessionSvc.setFuncSessionId(funcId, ss);
                }
                return ss;
            })
        );
    }

    /**
     * è¿å¥åè½ï¼è§¦åæå¡ç«¯é»è¾
     * ç§»é¤funcSession
     * è¿åå¬å±åétoken
     */
    private enterFunc(enterParam: FuncStateParam, su: string): Observable<any> {
        const bizContextUrl = this.FUNCSTATUS_PATH + (!su ? '' : `?su=${su}`);
        return this.http.request('Post', bizContextUrl, FrmUtil.setHeader(null, { body: enterParam }))
            .pipe(
                map((v: any) => {
                    return v.token || v.sessionId;
                })
            );
    }

    /**
     * éåºåè½ï¼å¦æå­å¨bizContextï¼åç§»é¤
     */
    private quitFunc(options: AppOptions, su: string): Observable<any> {
        const body = {
            token: options.token,
            tabId: options.tabId,
            funcId: options.funcId,
            action: 'quit'
        };
        const bizContextUrl = this.FUNCSTATUS_PATH + (!su ? '' : `?su=${su}`);
        return this.http.request('Post', bizContextUrl, FrmUtil.setHeader(null, { body }));
    }

    /**
     * è·åå½ååºç¨æå±su
     */
    private getSu(url: string): string {
        const uriArr = url.split('/');
        return uriArr.length > 3 ? uriArr[3] : '';
    }

    /**
     * iframe urlæ¼æ¥ querystring åæ°
     */
    private buildQueryString(routurl: string, funcId: string, params): string {
        if (routurl.indexOf('?') < 0) {
            routurl += `?funcId=${funcId}`;
        } else {
            routurl += `&funcId=${funcId}`;
        }
        if (params) {
            Object.keys(params).forEach((propName: string, index) => {
                routurl += `${index === 0 ? '' : '&'}${propName}=${params[propName]}`;
            });
        }
        return routurl;
    }

    /**
     * url æ¼æ¥mapæ ¼å¼çåæ°
     */
    private buildQueryStringByMap(routurl: string, funcId: string, tabId: string, token: string, params: Map<string, string>): string {
        if (routurl.indexOf('?') < 0) {
            routurl += `?funcId=${funcId}`;
        } else {
            routurl += `&funcId=${funcId}`;
        }

        routurl += `&appType=${AppType.Menu}`;
        routurl += `&tabId=${this.getPageId(funcId, tabId)}`;
        routurl += !token ? '' : `&cvft=${token}`;
        if (params) {
            params.forEach((value, key) => {
                routurl += !key ? '' : `&${key}=${value}`;
            });
        }
        return routurl;
    }

    /**
     * è·åå½åèåé¡µé¢çå¯ä¸æ è¯
     */
    private getPageId(funcId: string, tabId: string): string {
        return tabId || funcId;
    }

    /**
     * é¡µé¢ç¬¬ä¸æ¬¡è¢«æå¼æ¶ï¼çæå¯ä¸æ è¯
     */
    private newPageId(funcId: string, tabId: string): string {
        return tabId ? `${funcId}_${tabId}` : funcId;
    }

    public eventFire(token: string, args: any) {
        const feb = EventUtil.getFrmEvent();
        if (!feb || typeof feb.post !== 'function') {
            return;
        }
        feb.post(token, args);
    }

    public eventListner(token: string, handler: (value: any) => void, options?: AppOptions) {
        const feb = EventUtil.getFrmEvent();
        if (!feb || typeof feb.on !== 'function') {
            return;
        }
        // èåçå¬èå¿é¡»è¦å£°æèªå·±çèº«ä»½ï¼æ¹ä¾¿æ¡æ¶ç®¡ç
        if (options && options.appType) {
            switch (options.appType.toLowerCase()) {
                case AppType.App:
                    const appObkey = EventUtil.appObsToken(options.appId, options.appEntrance, options.tabId);
                    feb.onWithObkey(token, appObkey, handler);
                    return;
                case AppType.Menu:
                    const funcObkey = EventUtil.funcObsToken(options.funcId, options.tabId);
                    feb.onWithObkey(token, funcObkey, handler);
                    return;
                default: break;
            }
        }
        feb.on(token, handler);
    }

    /** åå»ºçå¬è®¢é, ç»´æ¤ä¸ä¸ªçå¬èåè¡¨ï¼éå¯¹èåè§¦åäºä»¶ */
    public eventListner4Func(token: string, funcId: string, handler: (value: any) => void) {
        const feb = EventUtil.getFrmEvent();
        if (!feb || typeof feb.on !== 'function') {
            return;
        }
        feb.onWithObkey(token, funcId, handler);
    }

    /** åå»ºçå¬è®¢é, ç»´æ¤ä¸ä¸ªçå¬èåè¡¨ï¼éå¯¹åºç¨è§¦åäºä»¶ */
    public eventListner4App(token: string, appId: string, appEntrance: string, handler: (value: any) => void) {
        const feb = EventUtil.getFrmEvent();
        if (!feb || typeof feb.on !== 'function') {
            return;
        }
        const appObkey = EventUtil.appObsToken(appId, appEntrance);
        feb.onWithObkey(token, appObkey, handler);
    }

}
