import { Injectable } from '@angular/core';
import { FrameworkVariableService } from '../frmVariableService/framework-variable.service';
import { EventBusPipe } from '../../eventbus/frm-eventpipe';
import { Subject } from 'rxjs';
import { FrmSubjectOptions } from '../../shared/frm-util';

@Injectable({
    providedIn: 'root'
})
export class BroadcastingStationService {

    private subjectMaps: Map<string, EventBusPipe>;
    private frmVariableService: FrameworkVariableService;
    private frmSubject = 'frmBroadcast-subjects';

    constructor() {
        this.frmVariableService = new FrameworkVariableService();
        this.subjectMaps = this.frmVariableService.getFrmVariableByKey(this.frmSubject) || new Map<string, EventBusPipe>();
    }

    /**
     * æ³¨åä¸»é¢ï¼å¹¶è·åå½åä¸»é¢å¯ä¸æ è¯token
     * @param subjectCode ä¸»é¢ç¼ç ï¼å»ºè®®ææç¡®å«ä¹ï¼æ¹ä¾¿ç¡®è®¤å½åä¸»é¢çèè´£
     * codeä¸æ¯å¯ä¸æ è¯ï¼å³ä½¿ç¸åcodeæ¯æ¬¡æ³¨åé½ä¼çæä¸ä¸ªæ°çä¸»é¢ï¼è¿åå¼tokenæ¯å¯ä¸æ è¯
     * @param options ä¸»é¢æ³¨åæä½ï¼å¯ä»¥ç´æ¥å¯¹äºèå æ åºç¨æ³¨åä¸»é¢
     * @param customSub èªå®ä¹ä¸»é¢ï¼å¦æä¸ºnullæèä¸ä¼ ï¼é»è®¤ä¸ºSubject
     */
    public subjectRegister(subjectCode: string, options: FrmSubjectOptions, customSub?: Subject<any>): string {
        if (!customSub) {
            customSub = new Subject<any>();
        }
        let token: string;
        if (options && options.funcId) {
            token = options.funcId;
        } else if (options && options.appId && options.appEntrance) {
            token = `${options.appId}-${options.appEntrance}`;
        } else {
            token = this.createToken();
        }
        const pipe = new EventBusPipe(token, null, customSub);
        pipe.pipeCode = subjectCode;
        this.subjectMaps.set(token, pipe);
        this.frmVariableService.frameworkVariableReg(this.frmSubject, this.subjectMaps);
        return token;
    }

    /**
     * æ³¨åä¸»é¢ï¼å¯ç´æ¥è·åä¸»é¢å®ä½ï¼
     * æ¡æ¶ä¸æç®¡ç»´æ¤ï¼å¸¸ç¨äºééå±ä¸äºä»¶æ©å±æºå¶
     * @param options æ³¨ååæ°
     * @param customSub æ¯å¦èªå®ä¹ä¸»é¢
     */
    public customSubjectRegister(options: FrmSubjectOptions, customSub?: Subject<any>): Map<string, EventBusPipe> {
        const subMap = new Map<string, EventBusPipe>();
        if (!customSub) {
            customSub = new Subject<any>();
        }
        let token: string;
        if (options && options.customToken) {
            token = options.customToken;
        } else if (options && options.funcId) {
            token = options.funcId;
        } else if (options && options.appId && options.appEntrance) {
            token = `${options.appId}-${options.appEntrance}`;
        } else {
            token = this.createToken();
        }
        const pipe = new EventBusPipe(token, null, customSub);
        subMap.set(token, pipe);
        return subMap;
    }

    /**
     * å é¤ä¸»é¢
     * @param subjectToken ä¸»é¢å¯ä¸æ è¯
     */
    public subjectRemove(subjectToken: string) {
        this.subjectMaps.delete(subjectToken);
        this.frmVariableService.frameworkVariableReg(this.frmSubject, this.subjectMaps);
    }

    /**
     * éè¿æä¸ªä¸»é¢åéæ¶æ¯
     * @param subjectToken ä¸»é¢å¯ä¸çtokenæ è¯
     * @param info è¦åéçæ¶æ¯
     */
    public notify(subjectToken: string, info: any) {
        const sub = this.getSubject(subjectToken);
        if (!sub) {
            return;
        }
        sub.post(info);
    }

    /**
     * ä¸ºä¸»é¢æ·»å è§å¯èï¼å¹¶çå¬ä¸»é¢æ¶æ¯
     * @param subjectToken ä¸»é¢token
     * @param observerToken å½åçå¬ä¸»é¢çè§å¯èå¯ä¸æ è¯token
     * @param handler è§å¯èçå¬å°ä¸»é¢æ¶æ¯æ¶è¦æ§è¡çå¨ä½
     */
    public response(subjectToken: string, observerToken: string, handler: (value: any) => void) {
        const sub = this.getSubject(subjectToken);
        if (!sub) {
            return;
        }
        sub.subscribeWithObkey(observerToken, handler);
    }

    /**
     * è§å¯èåæ¶å¯¹ä¸»é¢çè®¢é
     * @param subjectToken ä¸»é¢å¯ä¸æ è¯
     * @param observerToken è§å¯èå¯ä¸æ è¯
     */
    public responseUnSubscribe(subjectToken: string, observerToken: string) {
        const sub = this.getSubject(subjectToken);
        if (!sub) {
            return;
        }
        sub.unSubscribeWithObkey(observerToken);
    }

    private getSubject(subjectToken: string): EventBusPipe {
        return this.subjectMaps.get(subjectToken);
    }

    /**
     * åå»ºå¯ä¸token
     */
    private createToken() {
        // tslint:disable-next-line: only-arrow-functions
        const S4 = function () {
            // tslint:disable-next-line:no-bitwise
            return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
        };
        return (S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4());
    }
}
