// tslint:disable-next-line:import-blacklist
import { BehaviorSubject, Subscription, Subject } from 'rxjs';
import { Injectable, Type } from '@angular/core';
import { IDisposable } from './types';

@Injectable()
export class EventBusPipe implements IDisposable {

    public eventSubject: Subject<any>;
    public observers: Map<string, Subscription>;
    public pipeCode: string;
    private subscription: Subscription;
    private onceSubscription: Subscription;

    constructor(private tokenValue: string, private parentEventPipeList: Array<EventBusPipe>, sub: Subject<any>) {
        this.eventSubject = sub || new BehaviorSubject<any>(null);
        this.subscription = new Subscription();
        this.onceSubscription = new Subscription();
        this.observers = new Map<string, Subscription>();

        if (this.parentEventPipeList) {
            this.parentEventPipeList.push(this);
        }
    }

    post(bizPara: any) {
        this.eventSubject.next(bizPara);
    }

    subscribe(eventHandler: (value: any) => void): IDisposable {
        this.subscription = this.eventSubject.subscribe((value) => {
            eventHandler(value);
        });
        return this;
    }

    subscribeWithObkey(observerKey: string, eventHandler: (value: any) => void): IDisposable {
        const observer = this.observers.get(observerKey);
        if (observer) {
            observer.unsubscribe();
        }
        const sub = this.eventSubject.subscribe((value) => {
            eventHandler(value);
        });
        this.observers.set(observerKey, sub);
        return this;
    }

    subscribeOnce(eventHandler: (value: any) => void): IDisposable {
        const self = this;
        const scription = this.eventSubject.subscribe((value) => {
            eventHandler(value);
            if (scription) {
                scription.unsubscribe();
            }
            self.parentEventPipeList = new Array<EventBusPipe>();
        });
        return this;
    }

    unSubscribe() {
        if (this.subscription) {
            this.subscription.unsubscribe();
            this.subscription = null;
        }
    }
    // æ³¨éä½¿ç¨onceæ¹æ³æ³¨åçè®¢éã
    unSubscribeForOnce() {
        this.onceSubscription.unsubscribe();
    }

    /**
     * åæ¶æè§å¯èçè®¢é
     * @param observerKey è§å¯èå¯ä¸æ è¯
     */
    unSubscribeWithObkey(observerKey: string) {
        const scription = this.observers.get(observerKey);
        if (scription) {
            scription.unsubscribe();
        }
    }


    matchEmitterToken(tokenValue: string): boolean {
        if (this.tokenValue && tokenValue && this.tokenValue !== tokenValue) {
            return false;
        }
        return true;
    }

    examByTargetToken(tokenValue: string): boolean {
        if (this.tokenValue !== tokenValue) {
            return false;
        }
        return true;
    }

    dispose(subscriber: Subscription): void {
    }
}
