export class FrmUtil {

    public static setHeader(bizContextId: string, options: any) {
        options.headers = {
            'Content-Type': 'application/json',
        };
        if (bizContextId) {
            const contextKey = 'X-CAF-Runtime-Context';
            options.headers[contextKey] = bizContextId;
        }
        return options;
    }

    public static getTabArray(): IframeLink[] {
        const iframeTabArr = 'iframeTabarray';
        const topWin = this.getTopWindow();
        let frmWin: Window = window;
        while ((!frmWin[iframeTabArr] || frmWin[iframeTabArr].length === 0) && frmWin !== topWin) {
            frmWin = frmWin.parent;
        }
        return frmWin[iframeTabArr] as IframeLink[] || [];
    }

    public static getTopWindow(): Window {
        let curWin: Window = window.self;
        try {
            while (curWin.location.origin === curWin.parent.location.origin) {
                if (curWin !== curWin.parent) {
                    curWin = curWin.parent;
                } else {
                    break;
                }
            }
        } catch (e) { }
        return curWin;
    }

    public static removeTab(index: number) {
        const iframeArr = this.getTabArray();
        iframeArr.splice(index, 1);
    }
}

export class EventUtil {

    /** eventToken str */
    public static FARRIS_APPS = 'all-gsp-farrisapps';
    public static FARRIS_APP_CLICK = 'farrisapp-click';
    public static ALL_GSP_FUNCS = 'all-gsp-functions';
    public static BEFORE_FUNC_CLOSE = 'beforeFuncCloseEvent';
    public static FUNC_CLOSED = 'FuncClosed';
    public static FUNC_SWITCH = 'funcSwitchEvent';
    public static AFTER_FRAME_LOAD = "after-iframe-load-event";
    private static eventBusKey = 'frmEventBus';

    private static frmVariable = new FrameworkVariableService();

    /** è·åæ¡æ¶event */
    public static getFrmEvent(): FrmEventBus {
        const eventBus = this.frmVariable.getFrmVariableByKey(this.eventBusKey);
        return eventBus as FrmEventBus;
    }

    public static setFrmEvent(feb: FrmEventBus) {
        this.frmVariable.frameworkVariableReg(this.eventBusKey, feb);
    }

    /**
     * è·åappå¯ä¸çè§å¯èæ è¯
     */
    public static appObsToken(appId: string, appEntrance: string, tabId?: string): string {
        return tabId || `${appId}#${appEntrance}`;
    }

    /**
     * è·åfuncå¯ä¸çè§å¯èæ è¯
     */
    public static funcObsToken(funcId: string, tabId?: string): string {
        return tabId || funcId;
    }

    /**
     * è·åæäºä»¶ççå¬è
     */
    public static getObserver(eventToken: string): Map<string, Subscription> {
        const feb = this.getFrmEvent();
        const eventMap = feb.eventMap.get(eventToken);
        if (!eventMap) {
            return null;
        }

        let eventPipe: EventBusPipe;
        for (const eventPipeDetail of eventMap) {
            if (eventPipeDetail.matchEmitterToken(eventToken)) {
                eventPipe = eventPipeDetail;
                break;
            }
        }
        if (!eventPipe) {
            return null;
        }
        const obs = eventPipe.observers;
        return obs;
    }

    /**
     * æ¸ç©ºçå¬è
     */
    public static clearObserver(obKey: string) {
        const switchObsMap = this.getObserver(this.FUNC_SWITCH);
        const switchObs = switchObsMap.get(obKey);
        if (switchObs) {
            switchObs.unsubscribe();
            switchObsMap.delete(obKey);
        }
        const closeObsMap = this.getObserver(this.FUNC_CLOSED);
        const closeObs = closeObsMap.get(obKey);
        if (closeObs) {
            closeObs.unsubscribe();
            closeObsMap.delete(obKey);
        }
        const beforeCloseMap = this.getObserver(this.BEFORE_FUNC_CLOSE);
        const beforeClose = beforeCloseMap.get(obKey);
        if (beforeClose) {
            beforeClose.unsubscribe();
            beforeCloseMap.delete(obKey);
        }
    }

    /**
     * æ¸ç©ºæä¸ªç®¡é
     */
    public static clearEventPipe(pipeToken: string) {
        const feb = this.getFrmEvent();
        const eventPipes = feb.eventMap.get(pipeToken);
        if (!eventPipes) {
            return;
        }
        const eventPipe = eventPipes.find(item => item.examByTargetToken(pipeToken));
        if (eventPipe) {
            eventPipe.unSubscribe();
            feb.eventMap.delete(pipeToken);
        }
    }
}

